@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{margin:0;padding:0;font-family:"Roboto", sans-serif;font-size:1rem;box-sizing:border-box}a{color:#fff}.page{padding:1rem}.header{display:flex;justify-content:space-between;align-items:center;margin-bottom:2rem;padding-bottom:0.5rem;border-bottom:1px solid #eee}.header__title{font-size:1.5rem;color:#a349a4}.header__search{padding:0.5rem;width:20rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.header__search:focus{border-color:#a349a4;background-color:#fff}.contact__list{display:flex;justify-content:space-between;flex-wrap:wrap}.contact__item{margin-bottom:1rem;padding:1rem;width:20rem;list-style:none;border-radius:0.5rem;border:1px solid #ddd;background-color:#a349a4;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px;font-weight:bold;color:#fff}.contact__item>*{margin:3px 0}.contact__label{display:inline-block;font-weight:normal;width:5rem;color:#fff}.contact__name{font-weight:bold}.new-contact__title{padding:3rem 0 1rem 0;font-size:1.5rem;color:#a349a4}.new-contact__input{margin-right:1rem;margin-bottom:1rem;padding:0.5rem;width:10rem;border:2px solid #ddd;border-radius:0.5rem;background-color:#f8f8f8;outline:none;transition:all ease 0.3s}.new-contact__input:focus{border-color:#a349a4;background-color:#fff;box-shadow:rgba(99,99,99,0.2) 0px 2px 8px 0px}.new-contact__btn{padding:0.5rem 1.5rem;border-radius:0.5rem;border:none;border:2px solid #a349a4;background-color:#a349a4;font-weight:bold;color:#fff;cursor:pointer;transition:all ease 0.3s}.new-contact__btn:hover{border-color:#a349a4;background-color:#a349a4}

